import qs from 'qs'
export default {
    name: "addbills",
    data(){
        return{
            name:"",
            no:""
        }
    },
    methods:{
        add:function () {
            let token=JSON.parse(localStorage.getItem('user')).token;
            this.$axios.post('/user/invoice_store',
                qs.stringify({
                    name:this.name,
                    no:this.no
                }),{
                    headers: {
                        'Authorization': 'Bearer ' + token,

                    }
                }).then(res=>{
                let {err_code,msg,data}=res.data;
                if(err_code=='0'){
                    this.$router.push({name:'billslist'})
                }
            })
        }
    }
}



